<template>
  <div>
    <div class="subscribe-content">
      <h3 class="oracle-name text-color-gredient font-bold">
        <i class="el-icon-arrow-left font-bold previous-btn" @click="onGotoOracleListButtonClick"></i>
        <span style="font-size: 25px;">THAI LOTTERY</span>
      </h3>
      <div v-if="subscribeDetail.description !== ''"
           class="input-subscribe" style="text-align: center">
        <div class="font-bold" :class="{
          'subscribed': subscribeDetail.labelType === LABEL_TYPE_SUBSCRIBED,
          'non-subscribed': subscribeDetail.labelType === LABEL_TYPE_NON_SUBSCRIBED,
          'not-allow': subscribeDetail.labelType === LABEL_TYPE_NOT_ALLOW
        }">{{subscribeDetail.description}}</div>
        <div v-if="subscribeDetail.labelType === LABEL_TYPE_SUBSCRIBED"
             class="font-bold" style="color: #595959; font-size: 16px">
          Expire on: {{subscribeDetail.expire}}
        </div>
      </div>
      <div class="input-subscribe subscribe-address">
        <div class="font-bold input-subscribe-label">Address</div>
        <el-input v-model="dAppAddress" @input="onInputDAppAddress" placeholder="Enter Your Address"></el-input>
      </div>
      <div>
        <div>
          <el-button type="primary"
                     style="font-size: 20px; width: 100%; border-radius: 50px; padding: 15px 0"
                     @click="onOpenPopupPurchaseSubscribe"
                     :class="{'btn-disable': isDisableSubscribeButton()}"
                     :disabled="isDisableSubscribeButton()">
            SUBSCRIBE
          </el-button>
        </div>
      </div>
    </div>
    <div id="purchase-popup">
      <el-dialog :visible.sync="dialogVisible" width="500px" title="THAI LOTTERY">
        <div v-loading="loading">
          <div class="font-bold" style="margin: 10px 0">Min. purchase: {{purchaseSetting.minExtendTime}} days</div>
          <div style="color: #929292; position: relative">
            <el-input v-model="purchaseDay" @input="filterNumberOnly"></el-input>
            <div style="position: absolute; right: 20px; bottom: 9px; font-size: 25px; color: #7b7b7b">Day</div>
          </div>
          <div style="display: flex; justify-content: space-between">
            <div style="display: flex; justify-content: space-between; margin: 10px 0;">
              <div v-for="(day, index) of DEFAULT_PURCHASE_DAY_LIST" :key="`day-${index}`" style="margin-right: 10px">
                <el-button size="mini" @click="setPurchaseDay(day)">{{day}}</el-button>
              </div>
            </div>
            <div style="display: flex; justify-content: space-between; margin: 10px 0">
              <el-button size="mini" @click="setPurchaseDay(parseInt(purchaseSetting.minExtendTime))">Min</el-button>
              <el-button size="mini" @click="setPurchaseDay(365)">Max</el-button>
            </div>
          </div>
          <div style="text-align: center; margin: 10px 0">
            <div style="font-size: 30px; color: #7b7b7b">
              <i class="el-icon-bottom" style="background-color: #2a2a2a; border-radius: 50%; padding: 5px;"></i>
            </div>
            <div style="color: #77b0d4; height: 65px">
              <span v-if="parseInt(purchaseDay) > 0 && purchaseDay !== ''" style="font-size: 45px">{{payValue}}</span>
              <span v-else style="font-size: 45px"> N/A </span>
              <span style="font-size: 16px"> KOOKL</span>
            </div>
            <div class="font-bold" style="color: #7b7b7b">Expire: {{newExpireDate}}</div>
          </div>
          <div slot="footer" class="dialog-footer" style="display: flex; justify-content: space-between; border-top: 1px solid #202020; padding: 20px 0 15px;">
            <div style="text-align: left">
              <div style="font-size: 14px">Balance</div>
              <div style="color: var(--primary-color); font-size: 15px">
                <span>{{numberFormat(kookleBalanceWallet)}}</span>
                <span> KOOKL</span>
              </div>
            </div>
            <div style="display: flex; justify-content: space-between;">
              <div v-if="parseInt(allowance) === 0">
                <el-button @click="onApproveKookleTokenButtonClick"
                           type="primary" style="font-size: 17px; width: 100%; border-radius: 50px; padding: 13px 25px;">APPROVE</el-button>
              </div>
              <div style="margin-left: 10px">
                <el-button @click="onSubscribeOracleButtonClick"
                           :class="{'btn-disable': isDisableSubscribeButton() || payValue === 0 || parseInt(allowance) === 0}"
                           :disabled="isDisableSubscribeButton() || payValue === 0 || parseInt(allowance) === 0"
                           type="primary" style="font-size: 17px; width: 100%; border-radius: 50px; padding: 13px 25px;">PURCHASE</el-button>
              </div>
            </div>
          </div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import Web3 from 'web3'
import dateFormat from 'dateformat'

import { get } from 'lodash'
import { mapState } from 'vuex'
import { numberFormat } from '@/helpers'

import kookleTokenAbi from '@/abi/KookleToken.json'
import subscribeOracleAbi from '@/abi/SubscribeOracle.json'
import activeDirectoryAbi from '@/abi/ActiveDirectory.json'

const kookleTokenAddress = process.env.VUE_APP_CONTRACT_KOOKLE_TOKEN
const subscribeOracleAddress = process.env.VUE_APP_CONTRACT_SUBSCRIBE
const activeDirectoryAddress = process.env.VUE_APP_CONTRACT_ACTIVE_DIRECTORY
const oracleLotteryThaiAddress = process.env.VUE_APP_CONTRACT_ORACLE_LOTTERY_THAI
const bscScanUrl = process.env.VUE_APP_BSC_SCAN_URL

const DEFAULT_PURCHASE_DAY_LIST = [
  7, 15, 30, 90
]

const LABEL_TYPE_SUBSCRIBED = 1
const LABEL_TYPE_NON_SUBSCRIBED = 2
const LABEL_TYPE_NOT_ALLOW = 3

export default {
  name: 'Subscribe',
  props: {
    oracleName: {
      type: String,
      default: ''
    }
  },
  computed: {
    ...mapState({
      walletAddress: state => state.wallet.walletAddress
    })
  },
  data () {
    return {
      numberFormat,
      loading: false,
      DEFAULT_PURCHASE_DAY_LIST,
      LABEL_TYPE_SUBSCRIBED,
      LABEL_TYPE_NON_SUBSCRIBED,
      LABEL_TYPE_NOT_ALLOW,
      oracleLotteryThaiAddress,
      bscScanUrl,
      dialogVisible: false,
      dAppAddress: '',
      allowance: false,
      web3: null,
      kookleTokenInstance: null,
      subscribeOracleInstance: null,
      activeDirectoryInstance: null,
      kookleBalanceWallet: 0,
      purchaseSetting: {
        ratePerDay: 0,
        minExtendTime: 1
      },
      subscribeDetail: {
        isAllowSubscribe: false,
        labelType: LABEL_TYPE_SUBSCRIBED,
        description: '',
        expire: ''
      },
      newExpireDate: 'N/A',
      expireDateTimestamp: 0,
      payValue: 0,
      purchaseDay: '',
      isSubscribeButtonClick: false
    }
  },
  created: async function () {
    if (this.oracleName === '') {
      this.$router.push({
        name: 'OracleList'
      })
    }

    this.loading = true

    try {
      this.web3 = new Web3(window.ethereum)

      this.kookleTokenInstance = new this.web3.eth.Contract(kookleTokenAbi, kookleTokenAddress)
      this.subscribeOracleInstance = new this.web3.eth.Contract(subscribeOracleAbi, subscribeOracleAddress)
      this.activeDirectoryInstance = new this.web3.eth.Contract(activeDirectoryAbi, activeDirectoryAddress)

      this.allowance = await this.kookleTokenInstance.methods.allowance(this.walletAddress, subscribeOracleAddress).call()

      const purchaseSetting = await this.subscribeOracleInstance.methods.getPurchaseSetting(this.oracleName).call()
      const ratePerDay = get(purchaseSetting, '[0]', '1000000000000000000')

      this.purchaseSetting.ratePerDay = this.web3.utils.fromWei(ratePerDay)
      this.purchaseSetting.minExtendTime = get(purchaseSetting, '[1]', 1)
    } catch (err) {
      // this.$message({
      //   message: `Error`,
      //   type: 'error',
      //   duration: 5000
      // })
    }

    this.loading = false
  },
  methods: {
    onApproveKookleTokenButtonClick: async function () {
      this.loading = true

      try {
        const approveAmount = this.web3.utils.toWei('1000000000')
        await this.kookleTokenInstance.methods.approve(subscribeOracleAddress, approveAmount).send({
          from: this.walletAddress
        })

        this.allowance = await this.kookleTokenInstance.methods.allowance(this.walletAddress, subscribeOracleAddress).call()
      } catch (err) {
        this.$message({
          message: `Error`,
          type: 'error',
          duration: 5000
        })
      }

      this.loading = false
    },
    onSubscribeOracleButtonClick: async function () {
      if (this.purchaseDay === '') {
        alert('Please enter purchase day')
        return false
      }

      this.loading = true

      try {
        this.isSubscribeButtonClick = true

        const payValue = this.web3.utils.toWei(this.payValue.toString())
        const result = await this.subscribeOracleInstance.methods.purchaseByKookle(this.oracleName, this.dAppAddress, payValue).send({
          from: this.walletAddress
        })

        const txId = get(result, 'transactionHash', '')
        this.dialogVisible = false

        this.$message({
          message: `View detail on
            <a href="${bscScanUrl}/tx/${txId}" target="_blank" style="color: #333333">BscScan
                <i class="el-icon-link font-bold" style="font-size: 18px"></i>
            </a>
          `,
          type: 'success',
          dangerouslyUseHTMLString: true,
          duration: 10000
        })

        this.onInputDAppAddress()
      } catch (err) {
        this.$message({
          message: `Error`,
          type: 'error',
          duration: 10000
        })
      }

      this.isSubscribeButtonClick = false
      this.loading = false
    },
    onInputDAppAddress: async function () {
      this.subscribeDetail.isAllowSubscribe = false
      this.subscribeDetail.description = ''
      this.subscribeDetail.labelType = LABEL_TYPE_NOT_ALLOW
      this.subscribeDetail.expire = ''

      try {
        const dAppAddress = this.dAppAddress.trim()
        if (dAppAddress === '') {
          return
        }

        const verifyAddress = await this.web3.eth.getCode(dAppAddress)

        if (verifyAddress.toString() === '0x') {
          this.subscribeDetail.isAllowSubscribe = false
          this.subscribeDetail.description = 'THIS ADDRESS NOT ALLOW TO SUBSCRIBE'
          this.subscribeDetail.labelType = LABEL_TYPE_NOT_ALLOW
          this.subscribeDetail.expire = ''

          return
        }

        this.expireDateTimestamp = await this.activeDirectoryInstance.methods.getContractExpireTime(this.oracleName, dAppAddress).call()

        this.subscribeDetail.description = 'NON-SUBSCRIBED'
        this.subscribeDetail.labelType = LABEL_TYPE_NON_SUBSCRIBED

        if (parseInt(this.expireDateTimestamp) > 0) {
          const expireDate = new Date(parseInt(this.expireDateTimestamp) * 1000)

          this.subscribeDetail.description = 'SUBSCRIBED'
          this.subscribeDetail.labelType = LABEL_TYPE_SUBSCRIBED
          this.subscribeDetail.expire = dateFormat(expireDate, 'ddd, d mmmm yyyy HH:MM')
        }

        this.subscribeDetail.isAllowSubscribe = true
      } catch (err) {
        this.subscribeDetail.isAllowSubscribe = false
        this.subscribeDetail.description = 'INVALID ADDRESS FORMAT'
        this.subscribeDetail.labelType = LABEL_TYPE_NOT_ALLOW
        this.subscribeDetail.expire = ''
      }
    },
    onOpenPopupPurchaseSubscribe: async function () {
      this.loading = true
      this.purchaseDay = ''
      this.payValue = 0

      try {
        const kookleTokenBalance = await this.kookleTokenInstance.methods.balanceOf(this.walletAddress).call()
        this.kookleBalanceWallet = this.web3.utils.fromWei(kookleTokenBalance)

        this.dialogVisible = true
      } catch (err) {
        this.$message({
          message: `Error`,
          type: 'error',
          duration: 10000
        })
      }

      this.loading = false
    },
    filterNumberOnly: function () {
      this.purchaseDay = this.purchaseDay.replace(/[^0-9]+/g, '')
      this.calculateKookleByPurchaseDay()
      this.calculateExpireDate()
    },
    isDisableSubscribeButton: function () {
      const isDisabled = (this.dAppAddress === '') ||
                         (this.subscribeDetail.isAllowSubscribe === false) ||
                         (this.isSubscribeButtonClick === true) ||
                         (parseInt(this.purchaseDay) < parseInt(this.purchaseSetting.minExtendTime))

      return isDisabled
    },
    setPurchaseDay: function (numOfDay) {
      this.purchaseDay = numOfDay
      this.calculateKookleByPurchaseDay()
      this.calculateExpireDate()
    },
    calculateKookleByPurchaseDay: function () {
      const purchaseDay = (this.purchaseDay === '') ? 0 : parseInt(this.purchaseDay)
      const ratePerDay = parseInt(this.purchaseSetting.ratePerDay)

      this.payValue = purchaseDay * ratePerDay
      return this.payValue
    },
    calculateExpireDate: function () {
      if (this.purchaseDay === '') {
        this.newExpireDate = 'N/A'
        return
      }

      if (parseInt(this.expireDateTimestamp) > 0) {
        const date = new Date(parseInt(this.expireDateTimestamp) * 1000)
        date.setDate(date.getDate() + parseInt(this.purchaseDay))

        this.newExpireDate = dateFormat(date, 'ddd, d mmmm yyyy HH:MM')
      }
    },
    onGotoOracleListButtonClick: function () {
      return this.$router.push({
        name: 'OracleList'
      })
    }
  }
}
</script>

<style scoped>
  .btn-disable {
    opacity: 0.5;
  }

  .btn-disable:hover {
    opacity: 0.5 !important;
  }

  .oracle-name {
    text-align: center;
    position: relative;
    width: 100%;
    border-bottom: 1px solid #445872;
    margin: 0;
    padding: 20px 0;
  }

  .input-subscribe-label {
    position: absolute;
    z-index: 1;
    margin: 10px 20px;
    color: #868586;
  }

  .subscribe-content {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px 40px 40px 40px;
    border-radius: 20px;
    background-color: #000000ad;
  }

  .input-subscribe {
    margin: 40px 0;
  }

  .subscribed {
    color: greenyellow;
    font-size: 25px;
  }

  .non-subscribed {
    color: #74cfff;
    font-size: 25px;
  }
  .not-allow {
    color: red;
    font-size: 25px;
  }

  .previous-btn {
    font-size: 25px;
    float: left;
    margin-top: 5px;
    cursor: pointer;
  }
</style>
