<template>
  <div class="oracle-pool-card-list">
    <div class="oracle-pool-card">
      <div @click="gotoOracle(oracleNameLotteryThai)">
        <div style="position: relative">
          <el-avatar :size="100">
            <img class="kookle-coin-img" alt="kookl-coin" src="@/assets/images/kookl-coin.png">
          </el-avatar>
        </div>
        <div class="oracle-name text-color-gradient">
          THAI LOTTERY <i class="el-icon-arrow-right font-bold"></i>
        </div>
      </div>
      <div class="oracle-description">
        <div class="font-bold">
          <div>Min. Price <i class="el-icon-warning-outline"></i></div>
          <div>
            {{minPrice}} KOOKL /
            <span v-if="parseInt(minExtendTime) > 1">{{minExtendTime}} Days</span>
            <span v-else> Day</span>
          </div>
        </div>
        <div class="font-bold">
          <div>Total Subscribe</div>
          <div>{{totalSubscribe}} Address</div>
        </div>
      </div>
      <div class="font-bold" style="text-align: center; padding: 20px">
        <div style="cursor: pointer; color: var(--primary-color)"
             @click="isOpenOracleDetail[0] = !isOpenOracleDetail[0]">Details
          <i v-if="!isOpenOracleDetail[0]" class="el-icon-arrow-down font-bold"></i>
          <i v-else class="el-icon-arrow-up font-bold"></i>
        </div>
        <div v-if="isOpenOracleDetail[0]" style="color: #6096db">
          <div style="margin: 20px 0">
            <a :href="`${bscScanUrl}/address/${oracleLotteryThaiAddress}`" target="_blank" style="color: #6096db; text-decoration: none">
              View contract <img style="width: 15px; margin-left: 5px" alt="kookl-coin" src="@/assets/images/icon/foreign.png">
            </a>
          </div>
          <div>
            Get KOOKL <img style="width: 15px; margin-left: 5px" alt="kookl-coin" src="@/assets/images/icon/foreign.png">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Web3 from 'web3'
import { get } from 'lodash'
import subscribeOracleAbi from '@/abi/SubscribeOracle.json'
import activeDirectoryAbi from '@/abi/ActiveDirectory.json'

const subscribeOracleAddress = process.env.VUE_APP_CONTRACT_SUBSCRIBE
const oracleLotteryThaiAddress = process.env.VUE_APP_CONTRACT_ORACLE_LOTTERY_THAI
const activeDirectoryAddress = process.env.VUE_APP_CONTRACT_ACTIVE_DIRECTORY

const oracleNameLotteryThai = process.env.VUE_APP_ORACLE_NAME_LOTTERY_THAI
const bscScanUrl = process.env.VUE_APP_BSC_SCAN_URL

export default {
  name: 'SubscribeOracleList',
  data () {
    return {
      oracleNameLotteryThai,
      oracleLotteryThaiAddress,
      bscScanUrl,
      web3: null,
      isOpenOracleDetail: {
        0: false,
        1: false
      },
      subscribeOracleInstance: null,
      activeDirectoryInstance: null,
      ratePerDay: 0,
      minExtendTime: 0,
      minPrice: 0,
      totalSubscribe: 0
    }
  },
  created: async function () {
    try {
      this.web3 = new Web3(window.ethereum)
      this.subscribeOracleInstance = new this.web3.eth.Contract(subscribeOracleAbi, subscribeOracleAddress)
      this.activeDirectoryInstance = new this.web3.eth.Contract(activeDirectoryAbi, activeDirectoryAddress)

      const purchaseSetting = await this.subscribeOracleInstance.methods.getPurchaseSetting(oracleNameLotteryThai).call()
      const subscriber = await this.activeDirectoryInstance.methods.getSubscriber(oracleNameLotteryThai).call()

      const ratePerDayWei = get(purchaseSetting, '[0]', '1000000000000000000')
      const ratePerDay = this.web3.utils.fromWei(ratePerDayWei)

      this.minExtendTime = get(purchaseSetting, '[1]', 1)
      this.minPrice = parseInt(ratePerDay) * parseInt(this.minExtendTime)
      this.totalSubscribe = subscriber.length
    } catch (err) {
      // this.$message({
      //   message: `Error`,
      //   type: 'error',
      //   duration: 5000
      // })
    }
  },
  methods: {
    gotoOracle: function (oracleName) {
      this.$router.push({
        name: 'Subscribe',
        params: {
          oracleName: oracleName
        }
      })
    }
  }
}
</script>

