<script>
import Web3 from 'web3'
import LotteryAbi from '@/abi/DemoLotteryThai.json'

const BSC_TESTNET = process.env.VUE_APP_BSC_TESTNET_URL
const contractAddress = process.env.VUE_APP_CONTRACT_DEMO_LOTTERY_THAI
const PRIZE_LABEL = {
  first: 'First prize. 6,000,000฿ per prize',
  firstNeighbors: 'Extra prize. 100,000฿ per prize',
  second: 'Second prize. 200,000฿ per prize',
  third: 'Third prize. 80,000฿ per prize',
  fourth: 'Fourth prize. 40,000฿ per prize',
  fifth: 'Fifth prize. 20,000฿ per prize',
  firstThreeDigit: 'The first three digits. 4,000฿ per prize',
  lastThreeDigit: 'The last three digits. 4,000฿ per prize',
  lastTwoDigit: 'The Last two digits. 2,000฿ per prize'
}

export default {
  name: 'LotteryThai',
  components: {
  },
  data () {
    return {
      PRIZE_LABEL,
      isOpenPopoverCheckResult: false,
      isCheckLotteryResult: false,
      lotteryResults: [],
      contract: null,
      isViewAllPrize: true,
      firstPrizeArr: [],
      prizeData: {
        first: [],
        firstNeighbors: [],
        second: [],
        third: [],
        fourth: [],
        fifth: [],
        firstThreeDigit: [],
        lastThreeDigit: [],
        lastTwoDigit: []
      },
      contractResult: {},
      checkPrizes: [''],
      dateList: [
        {
          label: '16th March 2021',
          date: '2021-03-16'
        },
        {
          label: '1st March 2021',
          date: '2021-03-01'
        }
      ],
      selectedDate: '2021-03-16',
      selectedDrawDate: ''
    }
  },
  async created () {
    const options = {
      headers: [
        {
          name: 'Access-Control-Allow-Origin',
          value: BSC_TESTNET
        }
      ]
    }

    const web3 = new Web3(new Web3.providers.HttpProvider(BSC_TESTNET, options))
    this.contract = new web3.eth.Contract(LotteryAbi, contractAddress)

    await this.getLotteryResults(this.dateList[0].date)

    this.selectedDrawDate = this.dateList[0].label
    this.selectedDate = this.dateList[0].date
  },
  methods: {
    getLotteryResults: async function (date) {
      const contractResult = await this.contract.methods.getLotteryResults(date).call()
      const rawData = contractResult[0]

      const result = this.parsePrizeData(rawData)
      return result
    },
    parsePrizeData (prizeRaw) {
      this.prizeData.first = prizeRaw.slice(0, 1)
      this.prizeData.firstNeighbors = prizeRaw.slice(1, 3)
      this.prizeData.second = prizeRaw.slice(3, 8)
      this.prizeData.third = prizeRaw.slice(8, 18)
      this.prizeData.fourth = prizeRaw.slice(18, 68)
      this.prizeData.fifth = prizeRaw.slice(68, 168)
      this.prizeData.firstThreeDigit = prizeRaw.slice(168, 170)
      this.prizeData.lastThreeDigit = prizeRaw.slice(170, 172)
      this.prizeData.lastTwoDigit = prizeRaw.slice(172, 173)

      this.firstPrizeArr = this.prizeData.first[0].split('')
    },
    onToggleAllPrizeButtonClick: function () {
      this.isViewAllPrize = !this.isViewAllPrize
    },
    onSelectedDrawDate: async function () {
      await this.getLotteryResults(this.selectedDate)
    },
    onAddLotteryNumberButtonClick: function () {
      this.checkPrizes.push('')
    },
    onRemoveLotteryNumber: function (lotteryIndex) {
      if (this.checkPrizes.length === 1) {
        this.checkPrizes = ['']
        return
      }

      this.checkPrizes = this.checkPrizes.filter((item, index) => index !== lotteryIndex)
    },
    onCheckResultButtonClick: async function () {
      this.isCheckLotteryResult = true
      this.lotteryResults = []

      const checkPrizes = this.checkPrizes.filter(item => item !== '')
      if (checkPrizes.length === 0) {
        return
      }

      await this.getLotteryResults(this.selectedDate)

      checkPrizes.forEach((lotteryNumber, index) => {
        let isFoundResult = false

        Object.keys(this.prizeData).forEach(prize => {
          let number = lotteryNumber

          switch (prize) {
            case 'firstThreeDigit':
              number = lotteryNumber.slice(0, 3)
              break
            case 'lastThreeDigit':
              number = lotteryNumber.slice(3)
              break
            case 'lastTwoDigit':
              number = lotteryNumber.slice(4)
              break
            default:
              number = lotteryNumber
              break
          }

          const prizeResult = this.prizeData[prize].find(item => item === number)

          if (prizeResult !== undefined) {
            const data = {
              lotteryNumber: lotteryNumber,
              prizeLabel: PRIZE_LABEL[prize],
              status: true
            }

            this.lotteryResults.push(data)
            isFoundResult = true
          }
        })

        if (!isFoundResult) {
          const data = {
            lotteryNumber: lotteryNumber,
            prizeLabel: 'No Prize',
            status: false
          }

          this.lotteryResults.push(data)
        }
      })

      const selectedDate = this.dateList.find(item => item.date === this.selectedDate)
      this.selectedDrawDate = (selectedDate !== undefined) ? selectedDate.label : ''
      this.isOpenPopoverCheckResult = false

      window.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth'
      })
    },
    filterNumberOnly: function (index) {
      this.checkPrizes[index] = this.checkPrizes[index].replace(/[^0-9]+/g, '')
    }
  }
}
</script>
